.stopwatch {
  width: 85%;
  background-color: #eaeaea;
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 5% 10%;
  padding-top: 2%;
  padding-bottom: 2%;
  display: flex;
}

.circle {
  height: 270px;
  width: 270px;
  background-color: #72e59a;
  border: 2px solid;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-bottom: 2%;
  display: flex;
}

.time {
  font-size: 48px;
  font-weight: 300;
}

.controls {
  width: 350px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

#playButton {
  width: 137px;
  height: 60px;
}

#pauseButton {
  width: 150px;
  height: 60px;
  display: none;
}

#resetButton {
  width: 150px;
  height: 60px;
}

#playButton, #pauseButton, #resetButton {
  border-radius: 15px;
}

